/* Standard */
.ui.header {
  border: none;
  margin: calc(2rem -  0.165em ) 0em 1rem;
  padding: 0em 0em;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: normal;
  line-height: 1.33em;
  text-transform: none;
  color: #212121;
}
.ui.header:first-child {
  margin-top: -0.165em;
}
.ui.header:last-child {
  margin-bottom: 0em;
}
/*--------------
   Sub Header
---------------*/
.ui.header .sub.header {
  display: block;
  font-weight: normal;
  padding: 0em;
  margin: 0em;
  font-size: 1rem;
  line-height: 1.2em;
  color: rgba(0, 0, 0, 0.6);
}
/*--------------
      Icon
---------------*/
.ui.header > .icon {
  display: table-cell;
  opacity: 1;
  font-size: 2em;
  padding-top: 0.2em;
  vertical-align: top;
}
/* With Text Node */
.ui.header .icon:only-child {
  display: inline-block;
  padding: 0em;
  margin-right: 0.75rem;
}
/*-------------------
        Image
--------------------*/
.ui.header > .image,
.ui.header > img {
  display: inline-block;
  margin-top: 0.165em;
  width: 2.5em;
  height: auto;
  vertical-align: middle;
}
.ui.header > .image:only-child,
.ui.header > img:only-child {
  margin-right: 0.75rem;
}
/*--------------
     Content
---------------*/
.ui.header .content {
  display: inline-block;
  vertical-align: top;
}
/* After Image */
.ui.header > img + .content,
.ui.header > .image + .content {
  padding-left: 0.75rem;
  vertical-align: middle;
}
/* After Icon */
.ui.header > .icon + .content {
  padding-left: 0.75rem;
  display: table-cell;
  vertical-align: middle;
}
/*--------------
 Loose Coupling
---------------*/
.ui.header .ui.label {
  font-size: '';
  margin-left: 0.5rem;
  vertical-align: middle;
}
/* Positioning */
.ui.header + p {
  margin-top: 0em;
}
