.dimmable {
  position: relative;
}
.ui.dimmer {
  display: none;
  position: absolute;
  top: 0em !important;
  left: 0em !important;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.85);
  opacity: 0;
  line-height: 1;
  animation-fill-mode: both;
  animation-duration: 0.5s;
  transition: background-color 0.5s linear;
  user-select: none;
  will-change: opacity;
  z-index: 1000;
}
/* Dimmer Content */
.ui.dimmer > .content {
  width: 100%;
  height: 100%;
  display: table;
  user-select: text;
}
.ui.dimmer > .content > * {
  display: table-cell;
  vertical-align: middle;
  color: #FFFFFF;
}
/* Loose Coupling */
.ui.segment > .ui.dimmer {
  border-radius: inherit !important;
}
/*******************************
            States
*******************************/
.animating.dimmable:not(body),
.dimmed.dimmable:not(body) {
  overflow: hidden;
}
.dimmed.dimmable > .ui.animating.dimmer,
.dimmed.dimmable > .ui.visible.dimmer,
.ui.active.dimmer {
  display: block;
  opacity: 1;
}
.ui.disabled.dimmer {
  width: 0 !important;
  height: 0 !important;
}
